import { AppImage } from "components/atoms/app_image";
import { ImageList } from "constant/image_list";
import { InfiniteLooper } from "../infinity_loop";
import styles from "./index.module.scss";
import { ImageListSlide } from "constant/image";
import ImgsViewer from "components/morecules/react_image_viewer/src/ImgsViewer";
import { useState } from "react";

const list = ImageList.filter((item) => ImageListSlide.includes(item.id));
export const ImageInfinitySlide = () => {
  const [index, setIndex] = useState(0);
  const [isOpen, setIsOpen] = useState(false);
  return (
    <div className={styles.container}>
      <InfiniteLooper direction={"left"} speed={25}>
        {list.map((item, index) => {
          return (
            <div
              className={styles.image}
              key={item.id}
              onClick={() => {
                setIsOpen(true);
                setIndex(index);
              }}
            >
              <AppImage
                key={item.id}
                alt={`image ${item.id}`}
                src={item.thumbnail}
                width={68}
                height={86}
              ></AppImage>
            </div>
          );
        })}
      </InfiniteLooper>
      <ImgsViewer
        imgs={list}
        currImg={index}
        isOpen={isOpen}
        showThumbnails
        onClickPrev={() => {
          if (index === 0) {
            setIndex(list.length - 1);
          } else {
            setIndex(index - 1);
          }
        }}
        onClickNext={() => {
          if (index === list.length - 1) {
            setIndex(0);
          } else {
            setIndex(index + 1);
          }
        }}
        backdropCloseable
        onClose={() => {
          setIsOpen(false);
        }}
        onClickThumbnail={(i: any) => {
          setIndex(i);
        }}
      />
    </div>
  );
};
