import styles from "./index.module.scss";

interface Props {
  width: number;
  height: number;
  src: string;
  alt: string;
}
export const AppImage = ({ height, src, width, alt }: Props) => {
  return (
    <div className={styles.container}>
      <img
        alt={alt}
        className={styles.image}
        src={src}
        width={width}
        height={height}
        loading="lazy"
      ></img>
    </div>
  );
};
