import Lottie from "react-lottie";
import styles from "./index.module.scss";
import * as heart from "assets/lottie/heart4.json";

interface Props {
  value: any;
}

export const TotalDate = ({ value }: Props) => {
  return (
    <div className={`${styles.container} allowWidth`}>
      <div className={styles.info}>
        <div className={styles.title}>{"Chng ta  c nhau"}</div>
        <div className={styles.num}>
          {value}
          <div className={styles.sub}>{" Ngy"}</div>
        </div>
      </div>
      <div className={styles.animation2}>
        <Lottie options={{ animationData: heart }}></Lottie>
      </div>
    </div>
  );
};
