import Lottie from "react-lottie";
import * as heart from "assets/lottie/heart3.json";
import styles from "./index.module.scss";

interface Props {
  value: any;
  label: string;
}
export const CounterItem = ({ label, value }: Props) => {
  return (
    <div className={styles.container}>
      <Lottie options={{ animationData: heart }}></Lottie>
      <div className={styles.information}>
        <div className={styles.num}>{`${value}`}</div>
        <div className={styles.type}>{label}</div>
      </div>
    </div>
  );
};
