import { ProtectedRoute } from "components/morecules/protected_route";
import { Login } from "components/pages/login";
import { ListRoute } from "constant/routes";
import { AuthProvider } from "hooks/useAuth";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import "./App.css";

function App() {
  return (
    <div className="App">
      <BrowserRouter>
        <AuthProvider>
          <Routes>
            <Route path="/login" Component={Login}></Route>
            {ListRoute.map((item) => {
              return (
                <Route
                  key={item.id}
                  path={item.path}
                  Component={() => (
                    <ProtectedRoute>{item.component?.()}</ProtectedRoute>
                  )}
                ></Route>
              );
            })}
          </Routes>
        </AuthProvider>
      </BrowserRouter>
      {/* <Counter></Counter>
      <div
        onClick={() => {
          console.log(ref.current);
          ref.current.play();
        }}
      >
        namnx
      </div>
      <audio
        ref={ref}
        src="https://onlinetestcase.com/wp-content/uploads/2023/06/10-MB-MP3.mp3"
      ></audio> */}
      {/* <Login></Login> */}
    </div>
  );
}

export default App;
