import { CounterItem } from "components/morecules/counter_item";
import styles from "./index.module.scss";
import { Separator } from "components/atoms/separator";

interface Props {
  data: { value: any; label: string }[];
}
export const CounterList = ({ data }: Props) => {
  return (
    <div className={`${styles.container} allowWidth`}>
      <div className={styles.label}>
        {"Yu em tng gi, tng pht, tng giy"}
      </div>
      <Separator size="small"></Separator>
      <div className={styles.content}>
        {data.map((item) => {
          return (
            <CounterItem label={item.label} value={item.value}></CounterItem>
          );
        })}
      </div>
    </div>
  );
};
