interface Props {
  type?: "vertical" | "horizontal";
  size?: "tiny" | "small" | "medium" | "big" | "gigantic";
}
export const Separator = ({ size, type }: Props) => {
  const sizePx =
    size === "small"
      ? "5px"
      : size === "big"
      ? "20px"
      : size === "tiny"
      ? "2px"
      : size === "gigantic"
      ? "30px"
      : "10px";
  return (
    <div
      style={{
        padding: `${type === "horizontal" ? "0px" : sizePx} ${
          type === "horizontal" ? sizePx : "0px"
        }`,
      }}
    ></div>
  );
};
