import styles from "./index.module.scss";

interface Props {
  label: string;
  value: string;
  onChange: (e: any) => void;
  type?: "" | "password";
}
export const AppInput = ({ label, onChange, value, type }: Props) => {
  return (
    <div className={styles.container}>
      <div className={styles.label}>{label}</div>
      <input
        className={styles.input}
        onChange={onChange}
        value={value}
        type={type === "password" ? type : undefined}
      ></input>
    </div>
  );
};
