import { CounterList } from "components/organisms/counter/counter_list";
import { Header } from "components/organisms/counter/header";
import { ImageSlide } from "components/organisms/counter/image_slide";
import { StartDate } from "components/organisms/counter/start_date";
import { TotalDate } from "components/organisms/counter/total_date";
import { useCounterDate } from "hooks/useCounterDate";
import styles from "./index.module.scss";

export const CounterTemplate = () => {
  const { day, hour, minute, month, second, totalDay } = useCounterDate();
  const data = [
    { value: month, label: "Thng" },
    { value: day, label: "Ngy" },
    { value: hour, label: "Gi" },
    { value: minute, label: "Pht" },
    { value: second, label: "Giy" },
  ];

  return (
    <div className={styles.container}>
      <div className={styles.content}>
        <div className={styles.header}>
          <Header></Header>
        </div>
        <div className={styles.totalDate}>
          <TotalDate value={totalDay}></TotalDate>
        </div>
        <div className={styles.startDate}>
          <StartDate></StartDate>
        </div>
        <div className={styles.image}>
          <ImageSlide></ImageSlide>
        </div>
        <div className={styles.counterList}>
          <CounterList data={data}></CounterList>
        </div>

        {/* <Separator size="big"></Separator> */}
        {/* <Separator size="gigantic"></Separator>
        <TotalDate value={totalDay}></TotalDate>
        <Separator size="gigantic"></Separator>
        <StartDate></StartDate>
        <Separator size="gigantic"></Separator>
        <CounterList data={data}></CounterList> */}
      </div>
    </div>
  );
};
