import { ImageList } from "constant/image_list";
import styles from "./index.module.scss";
import { AppInput } from "components/atoms/input";
import { useContext, useEffect, useState } from "react";
import { Separator } from "components/atoms/separator";
import Lottie from "react-lottie";
import * as bear from "assets/lottie/bear.json";
import { useAuth } from "hooks/useAuth";

export const Login = () => {
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [showError, setShowError] = useState(false);
  const { login } = useAuth();
  const onLogin = () => {
    const userNameArr = [
      "trngthuhin",
      "truongthuhien",
      "thuhien",
      "thuhin",
    ];

    let checkUser = userNameArr.includes(
      `${userName}`.trim().replaceAll(" ", "").toLowerCase()
    );
    let checkPass = password.replace(/[^a-zA-Z0-9]/g, "") === "12081997";
    if (checkUser && checkPass) {
      login({ userName, password });
    } else {
      setShowError(true);
    }
  };

  useEffect(() => {
    if (showError) {
      setTimeout(() => {
        setShowError(false);
      }, 5000);
    }
  }, [showError]);

  return (
    <div
      className={styles.container}
      style={{
        // filter: "blur(8px)",
        // WebkitFilter: "blur(8px)",

        backgroundSize: "center",
        backgroundImage: `url(${
          ImageList.find(
            (item) => item.id === "AF1QipMw8bfejuIbNkTR61GwkFkddX2qQUVzUMexCBq_"
          )?.src
        })`,
      }}
    >
      <div className={styles.content}>
        <Lottie
          options={{ animationData: bear }}
          width={150}
          height={150}
        ></Lottie>
        <AppInput
          label="Tn Em"
          value={userName}
          onChange={(e) => {
            setUserName(e.target.value);
          }}
        ></AppInput>
        <Separator></Separator>
        <AppInput
          label="Ngy Thng Nm Sinh"
          value={password}
          type="password"
          onChange={(e) => {
            setPassword(e.target.value);
          }}
        ></AppInput>
        <Separator></Separator>
        <div
          className={styles.button}
          onClick={onLogin}
        >{`Nam i M Ca Ra`}</div>
        {showError && (
          <div>{"Bn khng phi ngi yu ca ti, i ch khc chi !!!"}</div>
        )}
      </div>
    </div>
  );
};
