import { ListRoute } from "constant/routes";
import styles from "./index.module.scss";
import { useNavigate } from "react-router-dom";

export const Content = () => {
  const navigate = useNavigate();
  return (
    <div className={`${styles.container} allowWidth`}>
      <div className={styles.menu}>
        {ListRoute.filter((x) => x.category).map((item) => {
          return (
            <div
              className={styles.item}
              onClick={() => {
                navigate(item.path);
              }}
            >
              <img
                className={styles.icon}
                alt={item.path}
                src={item.icon}
              ></img>
              <div className={styles.label}>{item.caption}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
};
