import * as heart from "assets/lottie/heart2.json";
import { AvatarWithName } from "components/morecules/avatar_with_name";
import { Config } from "constant/config";
import Lottie from "react-lottie";
import styles from "./index.module.scss";

export const Header = () => {
  return (
    <div className={`${styles.header} allowWidth`}>
      <AvatarWithName
        alt={Config.men.name}
        label={Config.men.name}
        src={Config.men.avatar}
      />
      <Lottie
        height={100}
        options={{ animationData: heart, autoplay: true, loop: true }}
        width={100}
        speed={0.5}
      ></Lottie>
      <AvatarWithName
        alt={Config.women.name}
        label={Config.women.name}
        src={Config.women.avatar}
      />
    </div>
  );
};
