import * as cute from "assets/lottie/cute.json";
import { Config } from "constant/config";
import moment from "moment";
import Lottie from "react-lottie";
import styles from "./index.module.scss";

export const StartDate = () => {
  return (
    <div className={`${styles.container} allowWidth`}>
      <div className={styles.content}>
        <div className={styles.cuteRevert}>
          <Lottie options={{ animationData: cute }}></Lottie>
        </div>
        <div className={styles.label}>{`${moment(
          Config.startDate,
          Config.dateValueFormat
        ).format(Config.dateDisplayFormat)}`}</div>
        <div className={styles.cute}>
          <Lottie options={{ animationData: cute }}></Lottie>
        </div>
      </div>
    </div>
  );
};
