import { Config } from "constant/config";
import moment from "moment";
import { useCallback, useEffect, useState } from "react";

export const useCounterDate = () => {
  const [month, setMonth] = useState(0);
  const [day, setDay] = useState(0);
  const [hour, setHour] = useState(0);
  const [minute, setMinute] = useState(0);
  const [second, setSecond] = useState(0);
  const [totalDay, setTotalDay] = useState(0);
  const startDate = moment(Config.startDate, Config.dateValueFormat);

  const setData = useCallback(() => {
    const duration = moment.duration(moment().diff(startDate));
    const getValue = (value: any) => {
      return parseInt(`${value}`) || 0;
    };
    setMonth(getValue(duration.months()));
    setDay(getValue(duration.days()));
    setHour(getValue(duration.hours()));
    setMinute(getValue(duration.minutes()));
    setSecond(getValue(duration.seconds()));
    setTotalDay(getValue(duration.asDays()));
  }, [startDate]);

  useEffect(() => {
    setData();
    const interval = setInterval(() => {
      setData();
    }, 1000);
    return () => {
      clearInterval(interval);
    };
  }, [setData]);
  return { month, day, hour, minute, second, totalDay };
};
